.search_container {
    width: 100%;
    height: 8vh;
    background-color: #EBF2F2;
    padding-top: 2%;
    justify-content: center;
    display: flex;
}

@media (max-width:1020px) {
    .search_container {
        justify-items: stretch;
        padding: 0;
        margin: 0;
    }
}